body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f5f5fb; }

.App._loading .app-main-body {
  visibility: hidden; }

.app-main-body {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 70px 30px 70px 30px; }

.error-msg {
  color: red;
  font-weight: bold; }

.App-header {
  position: fixed;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.07);
  height: 50px;
  background: #fff;
  width: 100%;
  z-index: 1000;
  padding: 0 15px 0 15px;
  display: flex;
  flex-direction: row;
  align-items: center; }

.pdf-wrapper {
  display: flex; }

.pdf-page {
  margin: 10px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.07); }

.pdf-data {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: flex-start; }
  .pdf-data * {
    margin: 0; }

#tagInfo {
  position: fixed;
  top: 50px;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background: #fff;
  transition: all .1s linear;
  border: none;
  padding: 5px 10px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.07);
  width: 300px; }
  #tagInfo .tag-prop {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start; }
    #tagInfo .tag-prop ._empty {
      color: grey; }
  #tagInfo .tag-info-title {
    font-weight: bold; }

.pdf-loading {
  font-weight: bold; }

.app-btn {
  border-radius: 3px;
  border: none;
  background: #f5f5fb;
  height: 30px;
  padding: 5px 8px;
  cursor: pointer;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.07);
  outline: none;
  margin: 0 10px; }
  .app-btn:hover {
    background: #e7e7ed; }
  .app-btn:active {
    background: #d1d1d7; }
  .app-btn:disabled {
    background: none;
    box-shadow: none; }
